import fotoRessenyaHistorica from "../../ressenya-historica.jpg";
import './QuiSom.css';

const QuiSom = () => {
    return (
        <div className="qui-som-container">
            <div className="position-relative">
                <img className="image-background-historic" src={fotoRessenyaHistorica} alt="Concert 100 anys naixement P. Irineu"/>
            </div>

            <div className="position-absolute-qui-som">
                <div className="gradient-title">
                    <h1 className="history-title title-large-screen">Ressenya histrica de la coral</h1>
                    <p className="text-description-qui-som">La "Coral d'Antics Escolans de Montserrat" va ser fundada l'any 1948 en
                        el si de la "Uni Escolans de Montserrat" per Leo Mass, Llus Ximenis i un grup de cantaires tots
                        ells antics escolans, encoratjats pel P. Ireneu Segarra, que va morir el 19 de novembre del 2005,
                        per que en tot moment n'ha estat un important valedor.
                    </p>
                </div>
                <div className="gradient">
                    <p className="text-description-qui-som">
                        La Coral s un cor de veus masculines i des de la seva creaci ha prestat sempre un mxim inters en
                        la interpretaci de msica litrgica tant en la seva vessant mondica amb el cant gregori, com
                        polifnica dels autors ms reconeguts, no oblidant tampoc els compositors del romanticisme i la
                        msica popular catalana.
                    </p>
                    <p className="text-description-qui-som">
                        Al llarg d'aquests 75 anys d'histria ha actuat arreu del pas, tamb en la resta de l'estat i en
                        diverses ciutats d'Europa.
                    </p>
                    <p className="text-description-qui-som">
                        Entre els cicles que cal destacar hi ha el Teatre Medieval del Tinell (Barcelona) i ms recentment
                        la participaci coral en varies operes en el Gran Teatre del Liceu ("L'holands errant",
                        "Billy Budd" i "Aida").
                    </p>
                    <p className="text-description-qui-som">
                        Ha collaborat ocasionalment amb la Capella de Msica i l'Escolania de Montserrat en algunes
                        celebracions litrgiques. Tamb ha realitzat enregistraments per la televisi i produccions
                        discogrfiques.
                    </p>
                    <p className="text-description-qui-som">
                        Des de l'any 2015 el seu director s en Guifr Canadell.
                    </p>
                </div>
            </div>
        </div>
    )
}
export default QuiSom;